import { defineDocumentType, makeSource } from 'contentlayer/source-files';
import remarkGfm  from 'remark-gfm'

/** @type {import('contentlayer/source-files').ComputedFields} */
const computedFields = {
    slug: {
        type: 'string',
        resolve: (doc) => doc._raw.flattenedPath,
    },
    // structuredData: {
    //     type: 'object',
    //     resolve: (doc) => ({
    //         '@context': 'https://schema.org',
    //         '@type': 'BlogPosting',
    //         title: doc.title,
    //         slug: doc.slug,
    //         date: doc.date,
    //         gist: doc.gist,
    //         list: doc.list,
    //         author: {
    //             '@type': 'Person',
    //             name: 'Rithik',
    //         },
    //     }),
    // },
};

export const Post = defineDocumentType(() => ({
    name: 'Post',
    filePathPattern: `**/*.md`,
    contentType: 'md',
    fields: {
        title: {
            type: 'string',
            required: true,
        },
        date: {
            type: 'string',
            required: true,
        },
        gist: {
            type: 'string',
            required: true,
        },
        list: {
            type: 'string',
            required: true,
        }
    },
    computedFields,
}));

export default makeSource({
    contentDirPath: '_posts',
    documentTypes: [Post],
    mdx: {
        remarkPlugins: [remarkGfm]
    }
});